.breadcrumbs {
  display: flex;
  align-items: center;
  margin-bottom: 35px;

  @include small-tablet {
    margin-bottom: 24px;
  }

  @include large-mobile {
    display: none;
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 14px;

      &::after {
        content: "/";
        position: absolute;
        right: -11px;
        top: 1px;
        color: #9a9ca5;
      }

      & .breadcrumbs__link:hover {
        color: var(--primary-color-hover);
      }
    }
  }

  &__link {
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: var(--primary-color);
    transition: color .3s ease-in-out;
  }
}
