.news {
  padding-top: 50px;
  padding-bottom: 65px;

  @include big-desktop {
    padding-top: 45px;
    padding-bottom: 30px;
  }

  @include small-tablet {
    padding-bottom: 10px;
    padding-top: 60px;
  }

  @include mobile {
    padding-top: 80px;
  }

  &__wrapper {

    &::after {
      content: '';
      clear: both;
    }
  }

  &__picture {
    float: left;
    padding-right: 36px;

    @include small-tablet {
      width: 250px;
      padding-right: 20px;
    }

    @include mobile {
      float: none;
    }
  }

  & .post-card__text {
    line-height: 22.5px;
  }
}

.news__wrapper {

  @include small-tablet {
    & .post-card__date {
      margin-top: 5px;
      margin-bottom: 10px;
    }

    & .post-card__text {
      padding-bottom: 0;
    }
  }
}
