.sort {
  margin-bottom: 50px;
  display: flex;
  align-items: center;

  @include big-desktop {
    margin-bottom: 30px;
  }

  @include mobile {
    flex-direction: column;
    align-items: flex-start;
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);

    @include mobile {
      margin-bottom: 15px;
    }
  }

  &__desr {

    @include mobile {
      display: flex;
      justify-content: space-between;
      width: 100%;
    }
  }

  &__btn {
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: var(--bg-color);
    opacity: 0.5;
    border: 1px solid #039583;
    box-sizing: border-box;
    border-radius: 2px;
    padding: 8px 31px;
    transition: opacity .3s ease-in-out;
    margin-left: 35px;

    @include mobile {
      margin-left: 0;
      padding: 8px 25px;

    }

    &:hover {
      opacity: 1;
      background-image: url(../img/sort-arr.svg);
      background-size: 20px 19px;
      background-repeat: no-repeat;
      background-position: 90% center;
      padding-right: 55px;

      @include mobile {
        padding-right: 45px;
      }
    }
  }
}

.active {
  opacity: 1;
  background-image: url(../img/sort-arr.svg);
  background-size: 20px 19px;
  background-repeat: no-repeat;
  background-position: 90% center;
  padding-right: 55px;

  @include mobile {
    padding-right: 45px;
  }
}
