.about {
  padding-top: 50px;
  padding-bottom: 100px;

  @include big-desktop {
    padding-top: 55px;
    padding-bottom: 30px;
  }

  @include small-tablet {
    padding-bottom: 10px;
  }

  @include mobile {
    padding-top: 80px;
  }

  &__title {
    font-family: 'BebasNeue';
    font-weight: 700;
    font-size: 36px;
    line-height: 38px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 35px;

    @include big-desktop {
      margin-bottom: 25px;
    }

    @include small-tablet {
      margin-bottom: 19px;
      font-size: 32px;
      line-height: 38px;
    }
  }

  &__descr {
    margin-bottom: 82px;

    @include large-desktop {
      margin-bottom: 65px;
    }

    @include big-desktop {
      margin-bottom: 60px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
    margin: 0;

    @include small-tablet {
      font-size: 18px;
      line-height: 22px;
    }

    &:not(:last-child) {
      margin-bottom: 27px;

      @include small-tablet {
        margin-bottom: 25px;
      }

      @include mobile {
        margin-bottom: 20px;
      }
    }
  }

  &__catalog {

    @include big-desktop {
      & .product__identical-img {
        transform: translateX(0%);
      }

      & .cards__title {
        margin-bottom: 5px;
      }
    }

    @include small-tablet {}

    &-title {
      font-family: 'BebasNeue';
      font-weight: 700;
      font-size: 32px;
      line-height: 38px;
      color: var(--primary-color);
      margin: 0;
      margin-bottom: 35px;

      @include small-tablet {
        font-size: 28px;
        line-height: 38px;
        margin-bottom: 55px;
      }
    }
  }
}

.benefit {
  margin-bottom: 100px;

  @include big-desktop {
    margin-bottom: 90px;
  }

  @include small-tablet {
    margin-bottom: 80px;
  }

  &__title {
    font-family: 'BebasNeue';
    font-weight: 400;
    font-size: 28px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 40px;

    @include small-tablet {
      font-size: 24px;
      line-height: 36px;
      margin-bottom: 26px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 95px 35px;

    @include large-desktop {
      gap: 65px 35px;
    }

    @include small-tablet {
      gap: 50px 35px;
    }

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
      gap: 45px 2px;
    }
  }

  &__item {
    grid-column: 3 span;
    font-family: 'BebasNeue';
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    position: relative;
    padding-top: 95px;

    @include small-tablet {
      grid-column: 4 span;
      font-size: 18px;
      line-height: 22px;

      &:last-child {
        grid-column: 6 span;

        @include small-tablet {
          grid-column: 4 span;
        }
      }

      &:nth-child(7) {
        grid-column: 6 span;
        justify-self: flex-end;
        padding-right: 45px;

        @include small-tablet {
          grid-column: 4 span;
          padding-right: 0;
          justify-self: flex-start;
        }
      }
    }

    @include mobile {
      grid-column: 4 span;
      padding-top: 0px;
      padding-left: 78px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 64px;
      height: 60px;
      background-image: url(../img/about-ok.svg);
      background-repeat: no-repeat;
      background-size: cover;

      @include mobile {
        width: 42px;
        height: 40px;
      }
    }
  }
}

.gallery {
  margin-bottom: 82px;

  @include small-tablet {
    margin-bottom: 42px;
  }

  &__title {
    font-family: 'BebasNeue';
    font-weight: 700;
    font-size: 32px;
    line-height: 38px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 35px;

    @include small-tablet {
      font-size: 28px;
      line-height: 38px;
      margin-bottom: 20px;
    }
  }

  &__content {
    padding-top: 60px;
    padding-left: 15px;
    padding-right: 15px;
    margin-top: -60px;
    margin-left: -15px;
    margin-right: -15px;

    @include small-tablet {
      padding-top: 35px;
      margin-top: -35px;
    }
  }
}

.video {
  position: relative;
  width: 100%;
  height: 880px;
  overflow: hidden;
  margin-bottom: 100px;

  @include large-desktop {
    height: 660px;
  }

  @include big-desktop {
    height: 528px;
    margin-bottom: 90px;
  }

  @include small-tablet {
    height: 334px;
    margin-bottom: 80px;
  }

  @include mobile {
    height: 190px;
    margin-bottom: 65px;
  }

  &--visible {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(42, 41, 40, 0.4);
      z-index: 1;
    }
  }

  & video {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 294px;
    height: 244px;
    transform: translate(-50%, -50%);
    color: var(--color-light);
    transition: color 0.3s ease-in-out;
    z-index: 2;

    @include small-tablet {
      width: 108px;
      height: 108px;
    }

    @include mobile {
      width: 60px;
      height: 60px;
    }

    &:hover {
      opacity: 0.7;
    }

    &--hidden {
      display: none;
    }
  }
}
