.custom-checkbox {
  @include custom-checkbox;
}

.product {
  padding-top: 50px;
  padding-bottom: 100px;

  @include big-desktop {
    padding-top: 45px;
    padding-bottom: 30px;
  }

  @include small-tablet {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @include mobile {
    padding-top: 80px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 30% 63%;
    gap: var(--grid-gap);
    margin-bottom: 30px;

    @include desktop {
      padding-top: 15px;
      gap: 10px 35px;
      grid-template-rows: 36% 63%;
      margin-bottom: 50px;
    }

    @include medium-desktop {
      grid-template-rows: auto;
      padding-top: 0;
      margin-bottom: 40px;
    }

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: auto;
      margin-bottom: 58px;
    }
  }

  &__image {
    grid-column: 4 span;
    grid-row: 1/3;
    justify-self: center;
    align-self: center;

    @include large-desktop {
      grid-column: 5 span;
      transform: translateY(-10px);
    }

    @include desktop {
      grid-column: 5 span;
      transform: translateY(-70px);
    }

    @include medium-desktop {
      grid-row: 1/2;
      grid-column: 4 span;
      transform: translateY(10px);
    }

    @include mobile {
      grid-row: 1/2;
      width: 250px;
      transform: translateY(50px);
      margin-bottom: 88px;
    }
  }

  &__parameter {
    grid-column: 8 span;
    grid-row: 1/2;

    @include large-desktop {
      grid-column: 7 span;
    }

    @include medium-desktop {
      grid-column: 8 span;
    }

    @include mobile {
      grid-row: 2/3;
      grid-column: 4 span;
    }
  }

  &__option {
    grid-column: 8 span;
    grid-row: 2/3;

    @include large-desktop {
      grid-column: 7 span;
    }

    @include medium-desktop {
      grid-column: 12 span;
      margin-top: 15px;
    }

    @include mobile {
      grid-column: 4 span;
      grid-row: 3/4;
    }

    &-title {
      font-family: 'BebasNeue';
      font-weight: 400;
      font-size: 22px;
      line-height: 38px;
      color: var(--primary-color);
      margin: 0;
      margin-bottom: 15px;

      @include medium-desktop {
        font-size: 20px;
      }
    }

    &-text {
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      color: var(--primary-color);
      margin: 0;

      @include medium-desktop {
        font-size: 18px;
      }

      &:not(:last-child) {
        margin-bottom: 6px;
      }
    }
  }

  &__cart {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    max-width: 870px;
    margin-top: 45px;

    @include large-desktop {
      margin-top: 35px;
    }

    @include desktop {
      margin-top: 30px;
    }

    @include medium-desktop {
      margin-top: 40px;
    }

    @include large-mobile {
      flex-wrap: wrap;
    }

    @include mobile {
      margin-top: 15px;
      flex-direction: column;
    }

    &-input {
      border: 1px solid rgba(3, 149, 131, 0.5);
      box-sizing: border-box;
      border-radius: 2px;
      width: 74px;
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      text-align: center;
      color: var(--primary-color);
      opacity: 0.5;
      padding: 7px 10px;
    }

    &-price {
      font-family: 'BebasNeue';
      font-weight: 700;
      font-size: 48px;
      line-height: 48px;
      color: var(--primary-color);
      margin: 0;
      transform: translate(30px, 5px);

      @include desktop {
        flex: 1 1 auto;
      }

      @include large-mobile {
        transform: translate(0);
        text-align: right;
      }

      @include mobile {
        text-align: left;
        margin-bottom: 12px;
      }

      &--text {
        font-size: 25px;
      }

      &--sum-hid {
        display: none;
        font-family: "ProximaNova", sans-serif;
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
        color: var(--primary-color);

        @include mobile {
          display: inline-block;
        }
      }
    }

    &-btn {
      background-color: var(--bg-nav-color);
      border-radius: 2px;
      display: inline-block;
      font-family: 'BebasNeue';
      font-weight: 700;
      font-size: 28px;
      line-height: 38px;
      color: var(--light-color);
      padding: 14px 0;
      min-width: 450px;
      transition: background-color 0.3s ease-in-out;

      @include desktop {
        min-width: 335px;
      }

      @include large-mobile {
        min-width: 100%;
      }

      @include mobile {
        font-size: 24px;
      }

      &:hover {
        background-color: var(--bg-nav-color-hover);
      }
    }
  }

  &__more {
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: var(--bg-nav-color);
    width: 80px;
    padding-bottom: 12px;

    @include mobile {
      font-size: 14px;
      width: 55px;
    }
  }

  &__identical {

    @include desktop {
      & .cards__title {
        margin-bottom: 0;
      }
    }

    @include medium-desktop {
      & .new__content {
        margin-top: -62px;
      }
    }

    &-title {
      font-family: 'BebasNeue';
      font-weight: 700;
      font-size: 32px;
      line-height: 38px;
      color: var(--primary-color);
      margin: 0;
      margin-bottom: 40px;

      @include medium-desktop {
        font-size: 28px;
      }
    }

    &-img {
      display: flex;
      justify-content: center;
    }
  }
}

.product-rigth {
  grid-column: 8 span;

  &__title {
    font-family: 'BebasNeue';
    font-weight: 400;
    font-size: 28px;
    line-height: 38px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 15px;

    @include medium-desktop {
      font-size: 24px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
    margin: 0;

    &:not(:last-child) {
      margin-bottom: 18px;

      @include mobile {
        margin-bottom: 15px;
      }
    }

    @include medium-desktop {
      font-size: 18px;
    }
  }
}

.product-checkbox {
  position: relative;
  display: none;

  @include desktop {
    margin-bottom: 10px;
  }

  @media (min-width: (2048px)) {
    &:nth-child(-n+5) {
      display: block;
    }
  }

  @media (min-width:767px) and (max-width:2048px) {
    &:nth-child(-n+3) {
      display: block;
    }
  }

  @media (min-width:576px) and (max-width:767.98px) {
    &:nth-child(-n+2) {
      display: block;
    }
  }

  @include mobile {
    &:nth-child(-n+1) {
      display: block;
    }
  }

  &:not(:last-child) {
    margin-right: 15px;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;

    @include desktop {
      margin-bottom: 10px;
    }

    @include medium-desktop {
      margin-bottom: 0;
    }
  }

  &__field {
    appearance: none;
    position: absolute;
  }

  &__content {
    padding-left: 25px;
    cursor: pointer;
    display: block;
    min-width: 146px;
    min-height: 40px;
    font-size: 20px;
    line-height: 1.8;
    color: rgba(3, 149, 131, .5);
    text-align: center;

    @include medium-desktop {
      min-width: 135px;
      font-size: 18px;
    }

    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      background-color: var(--light-color);
      border: 1px solid rgba(3, 149, 131, .5);
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
      border-radius: 2px;
      min-width: 146px;
      min-height: 40px;
      box-sizing: border-box;

      @include medium-desktop {
        min-width: 135px;
      }
    }

    &--1::before {
      content: "450x290x40";
    }

    &--2::before {
      content: "450x290x40";
    }

    &--3::before {
      content: "450x290x40";
    }

    &--4::before {
      content: "450x290x40";
    }

    &--5::before {
      content: "450x290x40";
    }

    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      min-width: 146px;
      min-height: 40px;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
      border: 1px solid rgba(3, 149, 131, 1);
      color: rgba(3, 149, 131, 1);

      @include medium-desktop {
        min-width: 135px;
      }
    }
  }
}

.product-checkbox__field:checked+.product-checkbox__content {
  color: rgba(3, 149, 131, 1);
}

.product-checkbox__field:checked+.product-checkbox__content::after {
  opacity: 1;
}

.product-checkbox__field:focus+.product-checkbox__content::before {
  opacity: 1;
}

.product-checkbox__field:disabled+.product-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

.option-checkbox {
  position: relative;
  margin-bottom: 45px;
  margin-right: 24px;
  display: none;

  @include large-desktop {
    margin-right: 22px;
  }

  @include desktop {
    margin-right: 14px;
  }

  @include medium-desktop {
    margin-right: 20px;
  }

  @media (min-width: (2048px)) {
    &:nth-child(-n+23) {
      display: block;
    }
  }

  @media (min-width:1921px) and (max-width:2048px) {
    &:nth-child(-n+15) {
      display: block;
    }
  }

  @media (min-width:576px) and (max-width:1920.98px) {
    &:nth-child(-n+13) {
      display: block;
    }
  }

  @include mobile {
    &:nth-child(-n+3) {
      display: block;
    }
  }

  &__wrapper {
    display: flex;
  }

  &__field {
    appearance: none;
    position: absolute;
  }

  &__content {
    padding-left: 25px;
    cursor: pointer;
    display: block;
    width: 81px;
    height: 64px;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
    text-align: center;
    border-radius: 2px;
    margin: 0;

    @include mobile {
      width: 58px;
      height: 69px;
      font-size: 18px;
    }

    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      background-image: url(../img/checkbox-bg-1.png);
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      border-radius: 2px;
      width: 81px;
      height: 64px;
      box-sizing: border-box;

      @include desktop {
        width: 70px;
      }

      @include mobile {
        width: 58px;
        height: 69px;
      }
    }

    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 81px;
      height: 64px;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
      color: rgba(3, 149, 131, 1);
      background-image: url(../img/checkbox-ok.svg);
      background-position: center;
      background-size: 20px 26px;
      background-repeat: no-repeat;
      border-radius: 2px;
      fill: blue;
      z-index: 20;

      @include desktop {
        width: 70px;
      }

      @include mobile {
        width: 58px;
        height: 69px;
      }
    }

    &-text {
      position: absolute;
      left: 10%;
      bottom: -30px;

      @include mobile {
        left: 0;

      }
    }
  }
}

.option-checkbox__field:checked+.option-checkbox__content,
.option-checkbox__field:checked+.option-checkbox__content {
  color: rgba(3, 149, 131, 1);
}

.option-checkbox__field:checked+.option-checkbox__content::after {
  opacity: 1;
  background-color: rgba(42, 41, 40, 0.3);
}

.option-checkbox__field:focus+.option-checkbox__content::before {
  opacity: 1;
}

.option-checkbox__field:disabled+.option-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

.product-descr {

  &__title {
    font-family: 'BebasNeue';
    font-weight: 400;
    font-size: 28px;
    line-height: 38px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 30px;

    @include medium-desktop {
      font-size: 24px;
      margin-bottom: 20px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 75px;

    @include large-desktop {
      margin-bottom: 65px;
    }

    @include medium-desktop {
      font-size: 18px;
      line-height: 26px;
    }
  }
}
