.shop-cart {
  padding-top: 50px;
  padding-bottom: 75px;

  @include large-mobile {
    padding-top: 95px;
  }

  @include mobile {
    padding-top: 80px;
  }

  &__title {
    font-family: 'BebasNeue';
    font-weight: 700;
    font-size: 36px;
    line-height: 38px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 46px;

    @include large-desktop {
      margin-bottom: 30px;
    }

    @include small-tablet {
      font-size: 32px;
      margin-bottom: 23px;
    }
  }

  &__list {
    margin-bottom: 55px;

    @include large-desktop {
      margin-bottom: 70px;
    }

    @include mobile {
      margin-bottom: 60px;
    }
  }

  &__item {
    background: #FFFFFF;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: var(--grid-gap);
    padding: 30px 70px 0px 45px;

    @include tablet {
      padding: 38px 45px 0 17px;
    }

    @include large-mobile {
      padding: 15px 10px 0 15px;
    }

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
      gap: 25px 35px;
    }

    &:not(:last-child) {
      margin-bottom: 45px;

      @include large-mobile {
        margin-bottom: 35px;
      }

      @include mobile {
        margin-bottom: 20px;
      }
    }
  }

  &__descr {
    grid-column: 6 span;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;

    @include large-desktop {
      flex-direction: column;
      grid-column: 5 span;
    }

    @include tablet {
      grid-column: 8 span;
    }

    @include mobile {
      grid-column: 4 span;
    }

    &-flex {
      display: flex;

      @include tablet {
        justify-content: space-between;
        width: 95%;
      }

      @include large-mobile {
        flex-direction: column;
      }
    }

    &-col {
      padding-top: 10px;

      @include large-desktop {
        padding-top: 30px;
      }

      @include large-mobile {
        padding-top: 25px;
      }

      @include mobile {
        padding-top: 0;
      }

      &:not(:last-child) {
        margin-right: 85px;

        @include large-desktop {
          margin-right: 70px;
        }

        @include mobile {
          margin-right: 0;
        }
      }
    }
  }

  &__item-title {
    font-family: 'BebasNeue';
    font-weight: 400;
    font-size: 28px;
    line-height: 38px;
    color: var(--primary-color);
    margin: 0;
    max-width: 291px;
    margin-right: 75px;

    @include large-desktop {
      margin-right: 0;
      max-width: 100%;
    }

    @include medium-desktop {
      font-size: 24px;
    }

    @include mobile {
      margin-bottom: 24px;
    }
  }

  &__item-text {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    margin: 0;
    color: var(--primary-color);
    grid-column: 2 span;
    flex: 1 1 auto;
    margin-bottom: 30px;
    align-self: center;

    @include large-desktop {
      grid-column: 3 span;
    }

    @include medium-desktop {
      font-size: 18px;
      line-height: 22px;
    }

    @include tablet {
      grid-column: 8 span;
    }

    @include mobile {
      grid-column: 4 span;
      margin-bottom: 22px;
    }

    & .image {
      margin-top: -15px;
      margin-left: 15px;
    }
  }

  &__img {
    grid-column: 2 span;
    transform: translateY(-10px);

    @include large-desktop {
      transform: translateY(10px);
    }

    @include tablet {
      grid-column: 4 span;
      transform: translateY(25px);

      & .image {
        width: 211px;
        height: 155px;
      }
    }

    @include mobile {
      grid-column: 4 span;
    transform: translateY(-10px);

      & .image {
        width: 236px;
        height: 195px;
      }
    }
  }

  &__price {
    font-family: 'BebasNeue';
    font-weight: 700;
    font-size: 48px;
    line-height: 48px;
    color: var(--primary-color);
    margin: 0;
    grid-column: 2 span;
    align-self: center;
    justify-self: end;

    @include medium-desktop {
      font-size: 38px;
    }

    @include tablet {
      grid-column: 4 span;
      margin-bottom: 20px;
      font-size: 42px;
    }

    @include mobile {
      justify-self: start;
    }

    &--text {
      font-size: 25px;
    }
  }

  &__input {
    border: 1px solid rgba(3, 149, 131, 0.5);
    box-sizing: border-box;
    border-radius: 2px;
    width: 74px;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: var(--primary-color);
    opacity: 0.5;
    padding: 7px 10px;
    margin-left: 15px;
    margin-right: 10px;
  }
}

.shop-form {

  &__wrapper {
    @include tablet {
      & .shop-cart__price {
        margin-bottom: 0;
      }
    }
  }

  &__content {
    background: #FFFFFF;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    padding: 30px 50px 35px;
    margin-bottom: 70px;

    @include big-desktop {
      margin-bottom: 20px;
      padding: 30px 50px 20px;
    }

    @include tablet {
      padding: 30px 15px 45px;
    }

    @include mobile {
      padding: 30px 15px 35px;
    }
  }

  &__title {
    font-family: 'BebasNeue';
    font-weight: 700;
    font-size: 28px;
    line-height: 38px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 27px;

    @include tablet {
      font-size: 24px;
      margin-bottom: 45px;
    }

    @include mobile {
      font-size: 22px;
      margin-bottom: 14px;
    }
  }

  &__label {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
  }

  &__result {
    display: flex;
    justify-content: flex-end;

    @include tablet {
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
    padding: 0;
    margin-right: 50px;

    @include tablet {
      margin-right: 25px;
      font-size: 18px;
    }

    @include mobile {
      max-width: 100px;
    }
  }

  &__btn {
    background-color: var(--bg-nav-color);
    border-radius: 2px;
    font-family: 'BebasNeue';
    font-weight: 700;
    font-size: 28px;
    line-height: 38px;
    color: var(--light-color);
    padding: 16px 114px;
    margin-left: 55px;
    transition: background-color .3s ease-in-out;

    @include tablet {
      font-size: 24px;
      margin-left: 0;
    }

    @include large-mobile {
      width: 100%;
    }

    @include mobile {
      padding: 16px 60px;
    }

    &:hover {
      background-color: var(--bg-nav-color-hover);
    }
  }

  &__offer {
    font-weight: 400;
    font-size: 10px;
    line-height: 16px;
    text-align: right;
    color: #C4C4C4;
    margin: 0;
    margin-bottom: 10px;
  }
}

.shop-form-row {

  &__top {
    display: flex;
    justify-content: space-between;

    @include medium-desktop {
      flex-direction: column;
      max-width: 800px;
      margin: 0 auto;
    }
  }

  &__top-label {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
    flex: 1 1 auto;

    @include large-desktop {
      &:first-child .shop-form-row__top-input {
        max-width: 330px;
      }
    }

    @include desktop {
      &:first-child .shop-form-row__top-input {
        max-width: 245px;
      }
    }

    @include medium-desktop {
      &:first-child .shop-form-row__top-input {
        max-width: 81%;
      }

      display: flex;
      justify-content: space-between;
      align-items: baseline;
    }

    @include mobile {
      display: block;

      &:first-child .shop-form-row__top-input {
        max-width: 100%;
      }
    }
  }

  &__top-input {
    border: 1px solid rgba(3, 149, 131, 0.5);
    border-radius: 2px;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
    opacity: 0.5;
    padding: 20px 30px;
    margin-left: 15px;
    max-width: 485px;
    width: 100%;
    margin-bottom: 30px;

    @include large-desktop {
      max-width: 325px;
    }

    @include desktop {
      max-width: 245px;
    }

    @include medium-desktop {
      max-width: 81%;
    }

    @include tablet {
      margin-bottom: 18px;
    }

    @include mobile {
      max-width: 100%;
      margin-left: 0;
      margin-top: 7px;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include medium-desktop {
      flex-direction: column;
      align-items: flex-start;
      max-width: 800px;
      margin: 0 auto;
    }
  }

  &__bottom-label {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
    flex: 1.75 1 auto;

    @include medium-desktop {
      width: 100%;
    }
  }

  &__bottom-input {
    border: 1px solid rgba(3, 149, 131, 0.5);
    border-radius: 2px;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: var(--primary-color);
    opacity: 0.5;
    padding: 20px 30px;
    margin-left: 15px;
    max-width: 1103px;
    width: 100%;

    @include desktop {
      max-width: 610px;
    }

    @include medium-desktop {
      max-width: 100%;
      margin-left: 0;
    }

    @include mobile {
      max-height: 120px;
    }
  }
}

.shop-form-checkbox {
  position: relative;
  flex: 0.90 1 auto;

  @include medium-desktop {
    margin-bottom: 20px;
  }

  &__field {
    appearance: none;
    position: absolute;
  }

  &__content {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #2A2928;
    padding-left: 40px;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      background: #FFFFFF;
      border: 1px solid rgba(3, 149, 131, 0.5);
      width: 20px;
      height: 20px;
      box-sizing: border-box;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 20px;
      height: 20px;
      box-sizing: border-box;
      background-image: url(../img/checkbox-ok.svg);
      background-position: center;
      background-size: 10px 10px;
      background-repeat: no-repeat;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }
  }
}

.shop-form-checkbox__field:checked+.shop-form-checkbox__content::after {
  opacity: 1;
}

.shop-form-checkbox__field:focus+.shop-form-checkbox__content::before {
  opacity: 1;
}

.shop-form-checkbox__field:disabled+.shop-form-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}
