.products {
  padding-top: 50px;
  padding-bottom: 100px;

  @include big-desktop {
    padding-top: 45px;
    padding-bottom: 80px;
  }

  @include small-tablet {
    padding-bottom: 60px;
  }

  @include mobile {
    padding-top: 80px;
  }

  & .cards {
    gap: var(--grid-gap-tablet);

    @include small-tablet {
      gap: 20px 25px;
    }
  }

  & .cards__wrapper {
    margin-top: 10px;
  }

  & .category__title {
    margin-bottom: 30px;

    @include big-desktop {
      margin-bottom: 20px;
    }

    @include mobile {
      font-size: 32px;
      line-height: 44px;
    }
  }

  & .cards__item {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include small-tablet {
      padding: 30px 14px 18px 17px;
    }

    @include mobile {
      padding: 30px 14px 37px 17px;
    }
  }

  & .cards__title {
    @include big-desktop {
      margin-bottom: 10px;
    }

    @include mobile {
      margin-bottom: 30px;
    }
  }

  & .cards__price {
    @include small-tablet {
      line-height: 1;
      align-self: flex-end;
    }
  }
}
