.category {
  padding-top: 45px;

  @include mobile {
    padding-top: 80px;
  }

  &__title {
    font-family: 'BebasNeue';
    font-weight: 700;
    font-size: 36px;
    line-height: 38px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 40px;

    @include big-desktop {
      margin-bottom: 26px;
    }

    @include small-tablet {
      font-size: 32px;
      line-height: 38px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: var(--grid-gap-tablet);
    margin-bottom: 100px;

    @include large-desktop {
      margin-bottom: 80px;
    }

    @include big-desktop {
      gap: 30px 30px;
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
      margin-bottom: 63px;
      gap: 25px 25px;
    }
  }

  &__item {
    grid-column: 6 span;
    background: var(--light-color);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    padding: 40px 30px 47px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    min-height: 233px;

    @include medium-desktop {
      grid-column: 12 span;
    }

    @include small-tablet {
      min-height: 186px;
      padding: 40px 30px 37px 17px;
    }

    @include large-mobile {
      align-items: center;
    }

    @include mobile {
      align-items: flex-start;
      grid-column: 1 span;
      padding: 30px 30px 26px 17px;
      min-height: 312px;
    }

    &:first-child .category__item-title {
      @include desktop {
        max-width: 300px;
      }

      @include medium-desktop {
        max-width: 100%;
      }
    }
  }

  &__item-title {
    font-family: 'BebasNeue';
    font-weight: 400;
    font-size: 28px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 90px;

    @include desktop {
      max-width: 222px;
    }

    @include big-desktop {
      flex-grow: 1;
    }

    @include small-tablet {
      font-size: 24px;
      line-height: 36px;
      margin-bottom: 26px;
      max-width: 100%;
    }

    @include large-mobile {
      margin-bottom: 0;
    }
  }

  &__image {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);

    @include big-desktop {
      top: 53%;
    }

    @include medium-desktop {
      right: 5%;
    }

    @include small-tablet {
      top: 10px;
      transform: translateY(0);
      width: 220px;
    }

    @include large-mobile {
      position: relative;
      top: 0;
      right: 0;
      padding: 20px 0;
    }

    @include mobile {
      padding: 0;
      position: absolute;
      top: 55%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &__link {
    font-size: 20px;
    line-height: 24px;
    color: var(--bg-nav-color);
    position: relative;
    transition: opacity 0.3s ease-in-out;

    @include small-tablet {
      font-size: 18px;
      line-height: 22px;
    }

    &:hover {
      opacity: 0.7;
    }

    &::before {
      content: "";
      position: absolute;
      right: -63px;
      top: 8px;
      width: 45px;
      height: 11px;
      background-image: url(../img/arr-card.svg);
    }
  }
}
